import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { provider } from 'web3-core'
import referralAbi from 'config/abi/referral.json'
import { getContract } from 'utils/web3'
import { getTokenBalance } from 'utils/erc20'
import {getCakeAddress, getReferralAddress} from 'utils/addressHelpers'
import useRefresh from './useRefresh'


export const useReferralCount = () => {
    const { account } = useWallet()
    const [balance, setBalance] = useState(new BigNumber(0))
    const { slowRefresh } = useRefresh()

    useEffect(() => {
        if(account) {
            const fethReferralCount = async () => {
                const cakeContract = getContract(referralAbi, getReferralAddress())
                const bal = await cakeContract.methods.referralsCount(account).call()
                console.log('testttt', bal)
                setBalance(new BigNumber(bal))
            }

            fethReferralCount()
        }
    }, [account, slowRefresh])

    return balance
}

export default useReferralCount
